import _ from 'lodash'
import {
    grid,
    title,
    legend,
    tooltip,
    funnelSeries
} from '../config/config';
const moduleData = {
    series:[{
        name: "系列一",
        value: 40,
    }, {
        name: "系列二",
        value: 60,
    }, {
        name: "系列三",
        value: 30,
    }, {
        name: "系列四",
        value: 80,
    }],
}
function getOption(data=moduleData) {
    funnelSeries.data = data.series
    let option = {
        grid,
        legend,
        title,
        tooltip,
        series:funnelSeries
    }
    _.merge(option,{
        title:{
            text:"123",
            top:30,
            subtext:""
        }
    })
    return option
}
export default getOption