import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../pages/login/login'
import home from '../pages/home/home'
import charts from '../pages/charts/charts'
import jsapi from '../pages/jsapi/jsapi'

Vue.use(VueRouter)

const routes = [{
    path:"/login",
    component:login
},{
    path:"/home",
    component:home
},{
    path:"/charts",
    component:charts
},{
    path:"/jsapi",
    component:jsapi
}]

const router = new VueRouter({
    routes
})

export default router