import _ from 'lodash'
import {
    legend,
    grid,
    axisCategory,
    axisValue,
    tooltip,
    lineSeries
} from '../config/config'

// 数据结构,多条折线series赋多条数据
const moduleData = {
    categories:["类目一","类目二"],
    series:[{
        name:"系列一",
        data:[10,20]
    }]
}

function getOption(data=moduleData){
    let series = []
    data.series.map((item)=>{
        let seriesCopy = JSON.parse(JSON.stringify(lineSeries))
        // console.log(lineSeries)
        seriesCopy.name = item.name
        seriesCopy.data = item.data
        series.push(seriesCopy)
    })
    // console.log(series)
    let defs = {
        legend,
        grid,
        xAxis:axisCategory,
        yAxis:axisValue,
        tooltip,
        series:series
    }
    _.merge(defs,{
        xAxis:{
            data:data.categories
        },
        series:{
            data:[10,20]
        }
    })
    return defs
}

export default getOption;