<template>
    <div class="chart-container" :id="chartsId"></div>
</template>

<script>
import {
    getLineOption,
    getBarOption,
    getPieOption,
    getFunnelOption,
} from './option-creater/index.js'
export default {
    props:{
        chartType:{
            type:String,
            default:"line"
        },
        chartData:{
            type:Object,
            default:()=>{}
        }
    },
    data() {
        return {
            chartsId:"",
            myChart:null
        };
    },
    watch:{
        chartData:function(val){
            if(!this.myChart){
                this.myChart = this.initChartInstance(this.chartsId)
            }
            let option = this.getOption(val)
            this.myChart.setOption(option)
        }
    },
    created(){
        this.chartsId = "chart-"+this.chartType+new Date().getTime()+Math.random()
    },
    mounted(){
        this.myChart = this.initChartInstance(this.chartsId)
        let option = this.getOption(this.chartData)
        this.myChart.setOption(option)
    },
    methods: {
        initChartInstance:function(id){
            let dom = document.getElementById(id);
            let myChart = this.$echarts.getInstanceByDom(dom);
            if(myChart){
                myChart.clear();
            }else{
                myChart = this.$echarts.init(dom);
            }
            return myChart
        },
        getOption:function(data){
            let option = {}
            if(this.chartType === "line"){
                option = getLineOption(data)
            }else if(this.chartType === "bar"){
                option = getBarOption(data)
            }else if(this.chartType === "pie"){
                option = getPieOption(data)
            }else if (this.chartType === "funnel") {
                option = getFunnelOption(data)
            }
            return option
        }
    }
}
</script>
<style>
    .chart-container{
        width: 100%;
        height: 100%;
    }
</style>